import React from 'react';
import {
  Button,
  ButtonGroup,
  Input,
} from '@ui-kitten/components';

export const InputReferenceShowcase = () => {

  const inputRef = React.useRef();
  const [value, setValue] = React.useState('');

  return (
    <React.Fragment>

      <Input
        ref={inputRef}
        label={`Focused: ${inputRef.current.isFocused()}`}
        placeholder='Place your Text'
        value={value}
        onChangeText={setValue}
      />

      <ButtonGroup>
        <Button onPress={inputRef.current.focus}>FOCUS</Button>
        <Button onPress={inputRef.current.blur}>BLUR</Button>
      </ButtonGroup>

    </React.Fragment>
  );
};
